"use client";

import React from "react";
import { X } from "lucide-react";
import { AnimatePresence, motion } from "framer-motion";
import { Good } from "@/assets/svg/svg_icon";

interface StatusStep {
    id: number;
    title: string;
    date: string;
    completed: boolean;
    active: boolean;
    note?: string;
}

interface ItemStatusHistoryModalProps {
    isOpen: boolean;
    onClose: () => void;
    onCancelOrder?: () => void;
}

const ItemStatusHistoryModal: React.FC<ItemStatusHistoryModalProps> = ({
    isOpen,
    onClose,
    onCancelOrder,
}) => {
    const statusSteps: StatusStep[] = [
        {
            id: 1,
            title: "Order placed",
            date: "Monday 08th December, 2025",
            completed: true,
            active: false,
        },
        {
            id: 2,
            title: "Pending Confirmation",
            date: "Monday 08th December, 2025",
            completed: true,
            active: false,
        },
        {
            id: 3,
            title: "Waiting to be shipped",
            date: "Monday 08th December, 2025",
            completed: true,
            active: false,
        },
        {
            id: 4,
            title: "Shipped",
            date: "Monday 08th December, 2025",
            completed: true,
            active: false,
        },
        {
            id: 5,
            title: "Available for pickup",
            date: "Monday 08th December, 2025",
            completed: true,
            active: true,
            note: "You have 3 days to come pick up your item after which it will be cancelled and sent back to the seller.",
        },
        {
            id: 6,
            title: "Order Delivered",
            date: "Monday 08th December, 2025",
            completed: false,
            active: false,
        },
    ];

    return (
        <AnimatePresence>
            {isOpen && (
                <div className="fixed inset-0 z-[100] overflow-hidden">
                    {/* Backdrop */}
                    <motion.div
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        exit={{ opacity: 0 }}
                        onClick={onClose}
                        className="absolute inset-0 bg-black/40 backdrop-blur-[2px] cursor-pointer"
                    />

                    {/* Sidebar Content */}
                    <motion.div
                        initial={{ x: "100%" }}
                        animate={{ x: 0 }}
                        exit={{ x: "100%" }}
                        transition={{ type: "spring", damping: 30, stiffness: 300 }}
                        className="absolute right-0 top-0 h-full w-full max-w-[700px] bg-white shadow-2xl flex flex-col"
                    >
                        {/* Header */}
                        <div className="flex items-center justify-between p-6 border-b border-gray-100">
                            <div>
                                <h2 className="text-2xl font-semibold text-gray-900">
                                    Item Status History
                                </h2>
                            </div>
                            <button
                                onClick={onClose}
                                className="p-2 transition-colors cursor-pointer"
                            >
                                <X className="w-6 h-6 text-gray-400" />
                            </button>
                        </div>

                        {/* Status Timeline */}
                        <div className="flex-1 overflow-y-auto p-8">
                            <div className="relative">

                                {/* Status Steps */}
                                <div className="space-y-6">
                                    {statusSteps.map((step, index) => {
                                        const isLast = index === statusSteps.length - 1;

                                        return (
                                            <div key={step.id} className="relative flex gap-6">
                                                {/* Icon + Line */}
                                                <div className="relative flex flex-col items-center">
                                                    {/* Step Badge */}
                                                    <div className="relative z-10">
                                                        {step.completed ? (
                                                            <div
                                                                className={`w-6 h-6 rounded-[6px] flex items-center justify-center ${step.active ? "bg-[#077D58]" : "bg-gray-200"
                                                                    }`}
                                                            >
                                                                <Good
                                                                    className={`w-3 h-3 ${step.active ? "text-white" : "text-gray-500"
                                                                        }`}
                                                                />
                                                            </div>
                                                        ) : (
                                                            <div className="w-6 h-6 rounded-[6px] flex items-center justify-center border-2 bg-white">
                                                                <Good stroke="black" width={12} height={12} />
                                                            </div>
                                                        )}
                                                    </div>

                                                    {/* Connecting Line */}
                                                    {!isLast && (
                                                        <div
                                                            className={`mt-2 h-12 w-[2px] ${step.active
                                                                ? "border-l-2 border-dashed border-gray-300"
                                                                : "bg-gray-200"
                                                                }`}
                                                        />
                                                    )}
                                                </div>

                                                {/* Content */}
                                                <div className="flex-1">
                                                    <h3
                                                        className={`text-lg font-semibold ${step.active ? "text-[#077D58]" : "text-gray-900"
                                                            }`}
                                                    >
                                                        {step.title}
                                                    </h3>

                                                    <p className="text-lg font-medium text-gray-500 mt-1">
                                                        {step.date}
                                                    </p>

                                                    {step.note && (
                                                        <p className="text-lg font-medium text-gray-500 mt-1">
                                                            {step.note}
                                                        </p>
                                                    )}
                                                </div>
                                            </div>
                                        );
                                    })}

                                </div>
                            </div>
                        </div>

                        {/* Footer Action */}
                        <div className="p-6">
                            <button
                                onClick={onCancelOrder}
                                type="submit"
                                className="group w-full p-0.5 border border-[#003625] rounded-[16px] cursor-pointer disabled:opacity-50 disabled:cursor-not-allowed mb-5"
                            >
                                <div className="w-full bg-[#003625] text-white py-5 rounded-[14px] text-center font-semibold transition-colors duration-200 group-hover:bg-emerald-100 group-hover:text-[#052214]">
                                    Cancel Order
                                </div>
                            </button>
                        </div>
                    </motion.div>
                </div>
            )}
        </AnimatePresence>
    );
};

export default ItemStatusHistoryModal;